import React, { useState, useEffect} from 'react';
import axios from 'axios';

import Header from '../extend/Header';
import Error404NotFound from '../extend/Error404NotFound';
import Error429TooManyRequests from '../extend/Error429TooManyRequests';
import '../../App.css';

import Post from './include/Post';


const PostDetailPage = (props) => {
    const categoryId = props.match.params.categoryId;
    const postId = props.match.params.postId;

    const [post, setPost] = useState(null);
    const [error, setError] = useState(false);

    useEffect(() => {
        axios.get("http://127.0.0.1:8000/api/v1/category/" + categoryId + "/")
            .then(response => {
                setPost(response.data);
            })
            .catch(err => {
                if (err.response.status) {
                    setError(
                    {
                        response: err.response.status
                    });
                }
            })
    }, []);

    console.log("Post is " + post);
    console.log("Error is " + error);
    console.log(post);

    if (error && !post) {
        return (
            <div>
                error
            </div>
        )
    } else if (!error && post) {
        return (
            <div>
                <Header />
                {"\n"}
                <div class="container">
                    <Post
                        id={post[0].id}
                        title={post[0].title}
                        text={post[0].text}
                        created_at={post[0].created_at}
                        user_data={post[0].user_data}
                        attachment={post[0].attachment}
                    />
                    {"\n"}
                </div>
            </div>
        )
    }
}

export default PostDetailPage;
